<template>
  <div>
    <section>
      <h3>Working Services</h3>
      <input type="checkbox" id="syr-working" v-model="working.BOT" />
      <label for="syr-working">The Bot</label>
      <input type="checkbox" id="syr-working" v-model="working.Syriatel" />
      <label for="syr-working">Syriatel</label>
      <input type="checkbox" id="mtn-working" v-model="working.MTN" />
      <label for="mtn-working">MTN</label>
      <input type="checkbox" id="adsl-working" v-model="working.Adsl" />
      <label for="adsl-working">Adsl</label>
    </section>
    <section>
      <h3>Set USD price here</h3>
      <label for="crypto">Crypto :</label>
      <input type="number" id="crypto" />
      <button @click="setPrice('crypto')">Set</button>
      <br />
      <label for="visa">Visa :</label>
      <input type="number" id="visa" />
      <button @click="setPrice('visa')">Set</button>
      <br />
      <label for="payeer">Payeer :</label>
      <input type="number" id="payeer" />
      <button @click="setPrice('payeer')">Set</button>
      <br />
      <label for="perfectMoney">Perfect Money :</label>
      <input type="number" id="perfectMoney" />
      <button @click="setPrice('perfectMoney')">Set</button>
    </section>
    <section>
      <h3>Broadcast</h3>
      <textarea id="broadcast" cols="30" rows="10"></textarea>
      <button @click="broadcast()">broadcast</button>
    </section>
    <article>
      <h3>Numbers Registered</h3>
      <ul>
        <li v-for="num in numbers" :key="num">{{ num }}</li>
      </ul>
    </article>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Controls",
  data() {
    return {
      numbers: [],
      working: this.$store.state.working,
    };
  },
  watch: {
    working: {
      deep: true,
      handler(newWOrkingObject) {
        axios.put(`${this.$store.state.server}/services`, {
          working: newWOrkingObject,
        });
      },
    },
  },
  async mounted() {
    let ns = await axios.get(`${this.$store.state.server}/payments/numbers`);
    if (ns.status === 200) {
      this.numbers = ns.data;
    } else {
      alert(ns.data);
    }
  },
  methods: {
    async setPrice(s) {
      try {
        let res = await axios.put(`${this.$store.state.server}/price/${s}`, {
          price: document.getElementById(s).value,
        });
        alert(res.statusText);
      } catch (err) {
        alert(
          "SOMETHING WENT WRONG PLEASE TRY AGAIN, AND CHECK YOUR CONNECTION OR PASSWORD"
        );
      }
    },
    async broadcast() {
      if (document.getElementById("broadcast").value) {
        try {
          let res = await axios.post(
            `${this.$store.state.server}/broadcast/all`,
            {
              message: document.getElementById("broadcast").value,
            }
          );
          alert(res.statusText);
        } catch (err) {
          alert(
            "SOMETHING WENT WRONG PLEASE TRY AGAIN, AND CHECK YOUR CONNECTION OR PASSWORD"
          );
        }
      }
    },
  },
};
</script>

<style lang="scss" scoped>
section {
  margin: 20px 0;
}
</style>
